<template>
  <div class="default-container">
    <Row :gutter="20">
      <Col span="8">
        <Card class="no-title">
          <Table :columns="todayColumns" :data="dealTodayClasses"></Table>
        </Card>
        <!-- eslint-disable -->
      </Col>
      <Col span="8">
        <Card class="no-title">
          <Table :columns="subjectColumns" :data="getSubjects"></Table>
        </Card>
        <!-- eslint-disable -->
      </Col>
      <Col span="8">
        <Card class="no-title">
          <Table :columns="classesColumns" :data="getClasses"></Table>
        </Card>
        <!-- eslint-disable -->
      </Col>
      <Col span="8">
        <Card class="no-title">
        </Card>
        <!-- eslint-disable -->
      </Col>
    </Row>
    <div class="echart-line">
      <div ref="dateEcharts"></div>
    </div>
  </div>
</template>

<script>
import { dates, students, subjects, classes } from '../mixins/index.js'
export default {
  mixins: [dates, students, subjects, classes],
  data () {
    return {
      data: [],
      studentsMap: {},
      today: '',
      todayClasses: [],
      todayColumns: [{
        title: '序号',
        type: 'index',
        width: 100,
        align: 'center'
      }, {
        title: '姓名',
        key: 'studentName',
        align: 'center'
      }, {
        title: '上课时间',
        key: 'periods',
        align: 'center'
      }, {
        title: '小时数',
        key: 'count',
        align: 'center'
      }],
      subjectColumns: [{
        title: '序号',
        type: 'index',
        width: 100,
        align: 'center'
      }, {
        title: '科目',
        key: 'label',
        align: 'center'
      }, {
        title: '学生数量',
        key: 'value',
        align: 'center'
      }],
      classesColumns: [{
        title: '序号',
        type: 'index',
        width: 100,
        align: 'center'
      }, {
        title: '科目',
        key: 'label',
        align: 'center'
      }, {
        title: '课时数量',
        key: 'value',
        align: 'center'
      }],
      subjectCount: { read: 0, hearing: 0, writing: 0, speaking: 0 }
    }
  },
  computed: {
    dealTodayClasses () {
      let classes = []
      let today = this.GT()
      classes = this.allClassData.filter(item => {
        return item.date === today
      })
      classes = classes.sort((x, y) => {
        return x.startTime - y.startTime
      })
      classes = classes.map(item => {
        let data = {
          ...item,
          periods: this.dealPeriods(item.periods),
          count: item.periods.length * 0.5
        }
        return data
      })
      return classes
    },
    getSubjects () {
      let data = []
      let obj = { ...this.subjectCount }
      for (const s of this.students) {
        ++obj[s.subject]
      }
      for (const k in obj) {
        data.push({
          label: this.subjectMaps[k],
          value: obj[k]
        })
      }
      return data
    },
    getClasses () {
      let data = []
      let obj = { ...this.subjectCount }
      for (const d of this.allClassData) {
        obj[d.subject] += (0.5 * d.periods.length)
      }
      for (const k in obj) {
        data.push({
          label: this.subjectMaps[k],
          value: obj[k]
        })
      }
      return data
    }
  },
  created () {
  },
  methods: {
  }
}
</script>

